  
  import { Component, OnInit, ViewChild } from '@angular/core';
  import { jqxGridComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxgrid';



@Component({
  selector: 'app-inventory-current-details',
  templateUrl: './inventory-current-details.component.html',
  styleUrls: ['./inventory-current-details.component.scss']
})
export class InventoryCurrentDetailsComponent implements OnInit {
  
 
    unitAllocationHeader:any
    unitAllocationData:any;
    gridSourceData:any;
  
    @ViewChild('datagrid', { static: false }) datagrid: jqxGridComponent;
  
    constructor() { }
  
    ngOnInit() {
      var cellsrenderer = (row, column, value) => {
        return '<div class="jqx-custom-inner-cell">' + value + '</div>';
      }
  
      var columnrenderer = (value) => {
        return '<div style="padding: 14px">' + value + '</div>';
      }


      this.unitAllocationHeader = [{
            text: 'Item No',
           datafield: 'slotType',
           width: 100,
           cellsrenderer: cellsrenderer,
           renderer: columnrenderer
       }, {
        text: 'Item Desription',
           datafield: 'slotBlockNo',
           minwidth: 100,
           cellsrenderer:cellsrenderer,
           renderer: columnrenderer
       }, {
        text: 'Warehouse ID',
           datafield: 'tempBlock',
           cellsrenderer:cellsrenderer,
           minwidth: 100,
           renderer: columnrenderer
       }, {
        text: 'Qty on Hand',
           datafield: 'tempUnit',
           cellsrenderer: cellsrenderer,
           minwidth: 100,
           renderer: columnrenderer
       }, {
        text: 'Status',
           datafield: 'startDateTime',
           cellsrenderer:cellsrenderer,
           minwidth: 100,
           renderer: columnrenderer
       }];
  
      //  var ticketListData = res.filter(data => {
      //   return data.isActive
      // })
  
     let localData=[
       {
        slotType:'101',
        slotBlockNo:'Waste Management',
        tempBlock:'05/15/2020',
        tempUnit:'05/15/2020',
        startDateTime:"On Time",
        endDateTime:"Responsilbe",
        slotRateType:"Active",
        Price:'1000'
       }
     ]
  
      this.gridSourceData = {
        localdata: localData,
        datatype: "array"
      }
  
      this.unitAllocationData = new jqx.dataAdapter(this.gridSourceData);
    }
  
  }
  