import { Router } from '@angular/router';

import { FormBuilder, FormArray, FormGroup, Validators } from '@angular/forms';

@Component({
	selector: 'app-inventory-setup',
	templateUrl: './inventory-setup.component.html',
	styleUrls: ['./inventory-setup.component.scss']
})
export class InventorySetupComponent implements OnInit {

	search: any = {};
	assetList: any = [];
	categoryList: any = [];
	subCategoryList: any = [];
	selectedItems: any = [];

	public assetSettings: any = {
		singleSelection: true,
		text: "Search Asset",
		selectAllText: 'Select All',
		unSelectAllText: 'UnSelect All',
		enableSearchFilter: true,
		badgeShowLimit: 3,
		// lazyLoading: true
	};
	public categorySettings: any = {
		singleSelection: true,
		text: "Search Category",
		selectAllText: 'Select All',
		unSelectAllText: 'UnSelect All',
		enableSearchFilter: true,
		badgeShowLimit: 3,
		// lazyLoading: true
	};
	public subCategorySettings = {
		singleSelection: true,
		text: "Search Sub Category",
		selectAllText: 'Select All',
		unSelectAllText: 'UnSelect All',
		enableSearchFilter: true,
		badgeShowLimit: 3,
		// lazyLoading: true
	};



	unitFieldType: string = "unitno";
	unitOrder: boolean = false;
	externalListData: any;
	externalData: string = "";
	isExternalDataLoaded: boolean = false;
	externalDeleteId: string = "";

	errorMessage: string = "";
	isError: boolean = false;


	userTable: FormGroup;
	control: FormArray;
	mode: boolean;
	touchedRows: any;



	public category = [
		{
			name: 'Gym Equipments',
			id: 1,
			subCategory: [
				{
					name: 'Tread Mill',
					id: 1,
				},
				{
					name: 'Ecliptical',
					id: 1,
				}
			],
		},
		{
			name: 'Computer Devices',
			id: 1,
			subCategory: [
				{
					name: 'Laptop',
					id: 1,
				},
				{
					name: 'Printer',
					id: 1,
				}
			],
		}

	]
	tempCategory = this.category;
	constructor(

		public dialog: MatDialog,
		private fb: FormBuilder
	) {

		// this.modalService = this.injector.get(ModalService);
	}




	filterCategoryItem(value) {
		this.category = this.tempCategory.filter((item: any) => item.name.toLowerCase().indexOf(value.toLowerCase()) > -1
		);
	}

	addSetUp(name, type, value, e): void {
		// let data:any={};
		// data.name=type;
		console.log(type, value);
		const dialogRef = this.dialog.open(AssetsAddSetupComponent, {
			width: '70%',
			data: {
				'name': name,
				'type': type,
				'value': JSON.stringify(value)
			}
		});

		dialogRef.afterClosed().subscribe(result => {
			console.log('The dialog was closed');
			console.log(result);
			if (result) {
				// this.getAllCategory();
			}
		});





	}



	// code  filter function 
	onItemSelect(item: any) {
		console.log(item);
		console.log(this.selectedItems);
	}
	OnItemDeSelect(item: any) {
		console.log(item);
		console.log(this.selectedItems);
	}
	onSelectAll(items: any) {
		console.log(items);
	}
	onDeSelectAll(items: any) {
		console.log(items);
	}

	ngOnInit(): void {
		this.isExternalDataLoaded = true;

		this.touchedRows = [];
		this.userTable = this.fb.group({
			tableRows: this.fb.array([])
		});
		this.addRow();
	}

	ngAfterViewInit() {
		this.control = this.userTable.get('tableRows') as FormArray;
	}

	sortUnitData(type) {
		this.unitFieldType = type;
		this.unitOrder = !this.unitOrder;
	}

	getFieldOrderBy(type) {
		if (this.unitFieldType == type) {
			return this.unitOrder ? 'desc' : 'asc';
		}
		else return '';
	}

	isMobileView() {
		return window.innerWidth <= 767 ? 'table-responsive' : '';
	}

	initiateForm(): FormGroup {
		return this.fb.group({
			serviceType: ['', Validators.required],
			email: ['', [Validators.email, Validators.required]],
			contactperson: ['', Validators.required],
			phone: ['', [Validators.required, Validators.maxLength(10)]],
			isEditable: [true]
		});
	}


	addRow() {
		const control = this.userTable.get('tableRows') as FormArray;
		control.push(this.initiateForm());
	}

	deleteRow(index: number) {
		const control = this.userTable.get('tableRows') as FormArray;
		control.removeAt(index);
	}

	editRow(group: FormGroup) {
		group.get('isEditable').setValue(true);
	}

	doneRow(group: FormGroup) {
		group.get('isEditable').setValue(false);
	}

	saveUserDetails() {
		console.log(this.userTable.value);
	}

	get getFormControls() {
		const control = this.userTable.get('tableRows') as FormArray;
		return control;
	}

	submitForm() {
		const control = this.userTable.get('tableRows') as FormArray;
		this.touchedRows = control.controls.filter(row => row.touched).map(row => row.value);
		console.log(this.touchedRows);
	}

}




















